 * @Description: -
 * @Author: -
 * @Date: -
 **/

import { Vue, Component, Prop, Watch } from "vue-property-decorator";
import api from "@/microApps/processManage/api";
import { ElForm } from "element-ui/types/form";

@Component({})
export default class FormDesign extends Vue {
  @Prop({ type: String, required: true })
  procdefKey!: string;

  useTemp: boolean = false; // 是否使用模板
  loading: boolean = true; // 是否使用模板
  searchFormName: string = "";
  formList: any[] = []; // 所有表单列表
  formId: string = ""; // 选中模板表单id
  form: { id?: string; formName: string; formNameStatus: boolean; formItems: any[]; formDef?: any } = {
    formName: "",
    formItems: [],
    formNameStatus: false // 名称检查状态
  };
  formRules: any = {
    formName: { required: true, message: "请输入表单名称", trigger: "blur" }
  };
  rules: any = {
    name: [
      {
        required: true,
        message: "请输入项目标题",
        trigger: "blur"
      },
      {
        validator: (rule: any, value: any, callback: any) => {
          if (this.form.formItems.some((d: any) => d.name === value)) {
            return callback(new Error("项目标题不要重复"));
          }
          callback();
        },
        trigger: "blur"
      }
    ]
  };
  typeOptions: Array<any> = [
    {
      value: "select",
      label: "单项选择"
    },
    {
      value: "multiSelect",
      label: "多项选择"
    },
    {
      value: "number",
      label: "数值"
    },
    {
      value: "image",
      label: "图片"
    },
    {
      value: "text",
      label: "文本"
    },
    {
      value: "textarea",
      label: "多行文本"
    },
    {
      value: "location",
      label: "定位"
    },
    {
      value: "link",
      label: "网页链接"
    }
  ];

  optionItem: any = {
    name: "",
    attrType: "",
    value: "",
    dataRange: {
      options: [],
      multiSelectedItems: []
    }
  };

  created() {
    this.getAllFormList().then(() => {
      this.getFormByProcess().finally(() => (this.loading = false));
    });
  }
  // 获取流程对应表单
  async getFormByProcess() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.processManage.getProcdefByKey, {
      params: {
        procdefKey: this.procdefKey
      }
    });
    if (status && data.formId) {
      this.formId = data.formId;
      this.useTemp = true;
    } else {
      this.useTemp = false;
    }
  }
  // 获取所有表单
  async getAllFormList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.formManage.getFormList, {
      params: {
        formName: this.searchFormName,
        size: 999999,
        current: 1
      }
    });
    if (status) {
      this.formList = data.records;
      this.$emit("data-load", this.formList);
    } else {
      this.$message.error(message);
    }
  }
  // 选择对应表单
  handleSelectForm(id: string) {
    this.form = this.formList.filter((i: any) => i.id === id)[0];
    this.form.formNameStatus = true;
    this.form.formItems = JSON.parse(this.form.formDef);
  }
  // 检查表单名称
  async checkFormName() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.formManage.checkFormName, {
      params: {
        formName: this.form.formName
      }
    });
    if (status) {
      if (!data) {
        this.form.formNameStatus = false;
        this.$message.warning("表单名称已使用");
      } else {
        this.form.formNameStatus = true;
        this.$message.success("表单名称可用");
      }
    } else {
      this.$message.error(message);
    }
  }
  // 数据项类型修改
  typeChange() {
    if (this.optionItem.attrType === "select" || this.optionItem.attrType === "multiSelect") {
      this.optionItem.dataRange.options = [];
      this.optionItem.dataRange.multiSelectedItems = [];
    } else if (this.optionItem.attrType === "number") {
      this.$set(this.optionItem.dataRange, "numMin", 0);
      this.$set(this.optionItem.dataRange, "numMax", 10);
      this.$set(this.optionItem.dataRange, "unit", "");
    } else if (this.optionItem.attrType === "text") {
      this.$set(this.optionItem.dataRange, "presetText", "");
    } else if (this.optionItem.attrType === "image") {
      this.$set(this.optionItem.dataRange, "imgMaxNum", 1);
    }
  }
  // 设置默认选项
  handleCheck(index: number) {
    if (this.optionItem.dataRange.options[index].defaultChosen && this.optionItem.attrType === "select") {
      for (let i in this.optionItem.dataRange.options) {
        this.optionItem.dataRange.options[i].defaultChosen = false;
      }
      this.optionItem.dataRange.options[index].defaultChosen = true;
    }
  }
  // 移除某项选项
  removeOptionItem(index: number) {
    this.optionItem.dataRange.options.splice(index, 1);
  }
  // 添加某个选项
  addOptionItem() {
    if (this.optionItem.attrType === "select" || this.optionItem.attrType === "multiSelect") {
      this.optionItem.dataRange.options.push({
        content: "",
        defaultChosen: false
      });
    } else {
      this.optionItem.dataRange.options.push({});
    }
  }
  // 添加一项表单项
  handleAddItem() {
    (this.$refs["optionItemForm"] as ElForm).validate((valid: boolean) => {
      if (valid) {
        (this.$refs["optionItemForm"] as ElForm).clearValidate();
        this.form.formItems.push(JSON.parse(JSON.stringify(this.optionItem)));
        this.optionItem = {
          name: "",
          attrType: "",
          value: "",
          dataRange: {
            options: [],
            multiSelectedItems: []
          }
        };
        this.returnData();
      } else {
        return;
      }
    });
  }

  returnData() {
    this.$emit("input", {
      mode: this.useTemp,
      form: this.form
    });
  }

  @Watch("useTemp", { immediate: true })
  handleChangeTemp(val: boolean) {
    if (val) {
      if (this.formId) this.handleSelectForm(this.formId);
    } else {
      this.form = { formName: "", formItems: [], formNameStatus: false };
    }
    this.returnData();
  }
  @Watch("form", { deep: true })
  handleChangeForm() {
    this.returnData();
  }
  // 保存新增表单
  async save(callback: any) {
    try {
      if (!this.form.formName) {
        this.$message.error("请输入表单名称");
        return callback();
      }
      if (!this.form.formItems.length) {
        this.$message.error("请添加项目");
        return callback();
      }
      if (!this.form.formNameStatus) {
        this.$message.error("请先验证名称是否可用");
        return callback();
      }
      let {
        data: { data, status, message }
      } = await this.$http.post(api.formManage.insertFormDefine, {
        formName: this.form.formName,
        formDef: JSON.stringify(this.form.formItems)
      });
      if (status) {
        callback(data);
      } else {
        this.$message.error(message);
        callback();
      }
    } catch (e) {
      callback();
    }
  }
}
