 * @Description: -
 * @Author: -
 * @Date: -
 **/

import { Vue, Component, Prop } from "vue-property-decorator";
import api from "@/microApps/processManage/api";

@Component({})
export default class TaskAssignment extends Vue {
  @Prop({ type: String, required: true })
  procdefId!: string;
  @Prop({ type: String, required: true })
  procdefKey!: string;
  @Prop({ type: Object, required: true })
  dataList!: any;

  loading: boolean = true;
  assignList: any[] = [];
  formList: any[] = [];
  invisibleOrgList: any[] = []; // 不可见组织
  invisibleRoleList: any[] = []; // 不可见角色
  invisibleUserList: any[] = []; // 不可见用户

  created() {
    if (this.dataList) {
      // 判断传入数据是否有组织列表数据
      if (this.dataList.invisibleOrgList) {
        this.invisibleOrgList = this.dataList.invisibleOrgList;
      } else {
        this.getInvisibleDataList(api.processManage.getAllOrg, "invisibleOrgList", "orgId", "orgName");
      }
      // 判断传入数据是否有角色列表数据
      if (this.dataList.invisibleRoleList) {
        this.invisibleRoleList = this.dataList.invisibleRoleList;
      } else {
        this.getInvisibleDataList(api.processManage.getAllRole, "invisibleRoleList", "roleId", "roleName");
      }
      // 判断传入数据是否有人员列表数据
      if (this.dataList.invisibleUserList) {
        this.invisibleUserList = this.dataList.invisibleUserList;
      } else {
        this.getInvisibleDataList(api.processManage.getAllUser, "invisibleUserList", "userId", "userName");
      }
      // 判断传入数据是否有表单列表数据
      if (this.dataList.processFormList) {
        this.formList = this.dataList.processFormList;
      } else {
        this.getAllFormList();
      }
      this.getAssignTaskList().then(() => (this.loading = false));
    } else {
      this.loadData();
    }
  }

  async loadData() {
    try {
      await Promise.all([
        this.getInvisibleDataList(api.processManage.getAllOrg, "invisibleOrgList", "orgId", "orgName"),
        this.getInvisibleDataList(api.processManage.getAllRole, "invisibleRoleList", "roleId", "roleName"),
        this.getInvisibleDataList(api.processManage.getAllUser, "invisibleUserList", "userId", "userName"),
        this.getAllFormList(),
        this.getAssignTaskList()
      ]);
    } finally {
      this.loading = false;
    }
  }
  // 获取任务节点
  async getAssignTaskList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.processManage.getAssignTaskList, {
      params: {
        processDefineId: this.procdefId
      }
    });
    if (status) {
      data.shift();
      this.assignList = data;
    } else {
      this.$message.error(message);
    }
  }
  // 获取所有表单
  async getAllFormList() {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.formManage.getFormList, {
      params: {
        size: 999999,
        current: 1
      }
    });
    if (status) {
      this.formList = data.records;
      this.$emit("data-load", this.formList);
    } else {
      this.$message.error(message);
    }
  }
  /**
   * 获取对应的不可见target数组的数据
   * @param url 请求地址
   * @param targetName 目标数组名称
   * @param propId id对应返回数据的key
   * @param propName name对应返回数据的key
   */
  async getInvisibleDataList(url: string, targetName: string, propId: string, propName: string) {
    let {
      data: { data, status, message }
    } = await this.$http.get(url, {
      params: {
        procdefKey: this.procdefKey
      }
    });
    if (status) {
      (this as any)[targetName] = data.map((i: any) => {
        return {
          id: i[propId],
          name: i[propName]
        };
      });
    } else {
      this.$message.error(message);
    }
  }

  // 指派流程的各个节点信息
  async saveUserTaskDesignator() {
    let status: boolean = true;
    return new Promise(async (resolve, reject) => {
      try {
        let requestBody: any[] = await this.checkUserTaskDesign();
        if (requestBody.every((item: any) => item.status)) {
          // 保存指派信息
          let {
            data: { status }
          } = await this.$http.post(api.processManage.saveUserTaskDesignator, requestBody);
          if (status) {
            // 修改流程状态
            this.$http
              .get(api.processManage.updateProcessStatus, {
                params: {
                  procdefKey: this.procdefKey,
                  status: 1
                }
              })
              .then((res: any) => {
                if (res.data.status) {
                  this.$message.success("发布成功");
                  resolve({ status: true });
                } else {
                  this.$message.error(res.data.message);
                  status = false;
                  reject({ status: false });
                }
              });
          } else {
            reject({ status: false });
          }
        } else {
          this.$message.error("节点指派不能为空");
          status = false;
          reject({ status: false });
        }
        return status;
      } catch (e) {
        reject({ status: false });
      }
    });
  }

  async checkUserTaskDesign() {
    let requestBody: any = this.assignList.map((item: any) => {
      if (item.userIds.length || item.roleIds.length || item.orgIds.length) {
        return {
          status: true,
          formId: item.formId,
          processDefineId: this.procdefId,
          userTaskId: item.userTask.id,
          personIds: item.userIds,
          roleIds: item.roleIds,
          orgIds: item.orgIds,
          assignedGroupId: item.userTask.candidateGroups[0],
          collectionType: "UNION" //目前只有并集
        };
      } else {
        return {
          status: false
        };
      }
    });
    return requestBody;
  }
}
