import { Vue, Component, Prop } from "vue-property-decorator";
import cookieUtils from "@/utils/cookie";
import api from "@/microApps/processManage/api";
import { ElUpload } from "element-ui/types/upload";

@Component({})
export default class FlowChartDesign extends Vue {
  @Prop({ type: String, required: false })
  procdefId!: string;

  procdefIdSelf: string = "";
  httpHeaders: any = {
    accessToken: "Bearer " + cookieUtils.get("accessToken")
  };
  uploadProcessFileUrl: string = api.processManage.uploadProcessFile;
  uploadParam: any = { importState: 0 };
  processFile: any = null;
  processFileList: any[] = [];
  bpmnPath: string = process.env.BASE_URL + "static/bpmn/index.html";
  getProcessImage: string = api.processManage.getProcessImage;
  frameLoading: boolean = false;
  loading = false;
  reDesign: boolean = false;
  changeFileStatus: boolean = true;

  mounted() {
    if (this.procdefId) {
      this.procdefIdSelf = this.procdefId;
      this.loadProcessImg();
    } else {
      this.initIframe();
    }
    this.frameLoading = false;
  }
  // 动态加载流程图
  loadProcessImg() {
    this.$nextTick(() => {
      let img: any = this.$refs["processImg"];
      let url = img.getAttribute("process-src");
      let request = new XMLHttpRequest();
      let accessToken = cookieUtils.get("accessToken");
      request.responseType = "blob";
      request.open("get", url, true);
      request.setRequestHeader("accessToken", "Bearer " + accessToken);
      request.onreadystatechange = e => {
        if (request.readyState == XMLHttpRequest.DONE && request.status == 200) {
          img.src = URL.createObjectURL(request.response);
          img.onload = () => {
            URL.revokeObjectURL(img.src);
          };
        }
      };
      request.send(null);
    });
  }
  // 重绘流程图
  handleReDesign() {
    this.reDesign = true;
    this.initIframe();
  }
  // 加载iframe页面
  initIframe() {
    this.frameLoading = true;
    this.$nextTick(() => {
      let iframe: any = this.$refs.iframe;
      iframe.contentWindow.addEventListener("load", () => {
        this.frameLoading = false;
        iframe.contentWindow.createNewDiagram();
      });
      iframe.contentWindow.finish = (data: any) => {
        this.saveProcessFile(data, 0);
      };
    });
  }
  // 直接保存已绘制的流程图
  async saveProcessFile(xmlData: any, importState: number) {
    this.loading = true;
    try {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.processManage.saveProcessFile, {
        text: xmlData,
        importState: importState
      });
      if (status) {
        if (data.code === 400) {
          this.$confirm(data.message, "提示", {
            distinguishCancelAndClose: true,
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }).then(() => {
            console.log(true);
            this.saveProcessFile(xmlData, 1);
          });
        } else {
          this.$message.success("导入成功");
          this.getProcdefById(data.data);
        }
      } else {
        this.$message.error(message);
      }
    } finally {
      this.loading = false;
    }
  }
  // 直接导入本地流程图文件
  handleUploadFileSuccess(data: any, file: any, fileList: any[]) {
    if (data.status) {
      if (data.data.code === 400) {
        (this.$refs["process-upload"] as ElUpload).clearFiles();
        this.processFileList = [];
        this.$confirm(data.data.message, "提示", {
          distinguishCancelAndClose: true,
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.uploadParam = {
            importState: 1
          };
          this.processFileList.push(this.processFile);
          this.$nextTick(() => {
            (this.$refs["process-upload"] as ElUpload).submit();
          });
        });
      } else {
        this.$message.success("导入成功");
        this.uploadParam = {
          importState: 0
        };
        this.getProcdefById(data.data.data);
      }
    } else {
      this.$message.error(data.message);
    }
  }
  // 根据返回的流程id查询数据返回父组件
  async getProcdefById(id: string) {
    let {
      data: { data, status, message }
    } = await this.$http.get(api.processManage.getProcdefById, {
      params: { id }
    });
    if (status) {
      this.$emit("complete", data);
    } else {
      this.$message.error(message);
    }
  }

  fileChanged(file: any) {
    // 修改文件状态，避免缓存造成无法上传
    this.processFile = {
      name: file.name + "-reUpload",
      raw: file.raw,
      size: file.size,
      status: "ready",
      uid: file.uid
    };
    (this.$refs["process-upload"] as ElUpload).submit();
  }
}
