 * @Description: -
 * @Author: -
 * @Date: -
 **/

import { Vue, Component, Prop, Model } from "vue-property-decorator";
import api from "@/microApps/processManage/api";

@Component({})
export default class RoleSet extends Vue {
  @Prop({ type: String, required: true })
  procdefKey!: string;
  @Prop({ type: String, required: true })
  type!: any;

  loading: boolean = true;

  typeId: string = this.type || "";
  typeOptions: any[] = [];
  invisibleOrgList: any[] = []; // 不可见组织
  invisibleRoleList: any[] = []; // 不可见角色
  invisibleUserList: any[] = []; // 不可见用户
  orgValue: any[] = []; // 可见组织
  roleValue: any[] = []; // 可见角色
  userValue: any[] = []; // 可见用户

  @Model("input")
  processRole!: any;

  created() {
    this.loadData();
  }

  mounted() {}

  async loadData() {
    try {
      await Promise.all([
        this.getAllProcessType(),
        this.getInvisibleDataList(api.processManage.getAllOrg, "invisibleOrgList", "orgId", "orgName"),
        this.getInvisibleDataList(api.processManage.getAllRole, "invisibleRoleList", "roleId", "roleName"),
        this.getInvisibleDataList(api.processManage.getAllUser, "invisibleUserList", "userId", "userName"),
        this.getVisibleDataList(api.processManage.getProcessVisibleOrg, "orgValue", "orgId"),
        this.getVisibleDataList(api.processManage.getProcessVisibleRole, "roleValue", "roleId"),
        this.getVisibleDataList(api.processManage.getProcessVisiblePerson, "userValue", "userId")
      ]);
    } finally {
      this.loading = false;
      this.$emit("data-load", {
        orgList: this.invisibleOrgList,
        roleList: this.invisibleRoleList,
        userList: this.invisibleUserList
      });
      this.returnData();
    }
  }

  returnData() {
    let processRoleSet = {
      orgValue: this.orgValue,
      roleValue: this.roleValue,
      userValue: this.userValue,
      type: this.typeId
    };
    this.$emit("input", processRoleSet);
  }

  // 获取所有流程类型
  async getAllProcessType() {
    let {
      data: { data, status, message }
    } = await this.$http.get(
      this.typeId.length ? `${api.processManage.getAllProcessTypeWithDelete}${this.typeId}` : api.processManage.getAllProcessType
    );
    if (status) {
      this.typeOptions = data;
    } else {
      this.$message.error(message);
    }
  }

  /**
   * 获取对应的不可见target数组的数据
   * @param url 请求地址
   * @param targetName 目标数组名称
   * @param propId id对应返回数据的key
   * @param propName name对应返回数据的key
   */
  async getInvisibleDataList(url: string, targetName: string, propId: string, propName: string) {
    let {
      data: { data, status, message }
    } = await this.$http.get(url, {
      params: {
        procdefKey: this.procdefKey
      }
    });
    if (status) {
      (this as any)[targetName] = data.map((i: any) => {
        return {
          id: i[propId],
          name: i[propName]
        };
      });
    } else {
      this.$message.error(message);
    }
  }

  /**
   * 获取对应的可见target数组的数据
   * @param url 请求地址
   * @param targetName 目标数组名称
   * @param propId id对应返回数据的key
   */
  async getVisibleDataList(url: string, targetName: string, propId: string) {
    let {
      data: { data, status, message }
    } = await this.$http.get(url, {
      params: {
        procdefKey: this.procdefKey
      }
    });
    if (status) {
      (this as any)[targetName] = data.map((i: any) => i[propId]);
    } else {
      this.$message.error(message);
    }
  }
}
