import { Vue, Component } from "vue-property-decorator";
import api from "@/microApps/processManage/api";
import cookieUtils from "@/utils/cookie";
import FlowChartDesign from "@/microApps/processManage/views/processManage/editComponents/FlowChartDesign.vue";
import RoleSet from "@/microApps/processManage/views/processManage/editComponents/RoleSet.vue";
import FormDesign from "@/microApps/processManage/views/processManage/editComponents/FormDesign.vue";
import TaskAssignment from "@/microApps/processManage/views/processManage/editComponents/TaskAssignment.vue";

@Component({
  components: { TaskAssignment, FormDesign, RoleSet, FlowChartDesign }
})
export default class ProcessEditStep extends Vue {
  currentData: any = null; // 传入流程信息
  typeId: string = "";
  state: 0 | 1 | 2 | 3 = 0; // 步骤码

  someRequestData: { [key: string]: any[] } = {
    invisibleOrgList: [], // 所有组织列表
    invisibleRoleList: [], // 所有角色列表
    invisibleUserList: [], // 所有用户列表
    processFormList: [] // 所有流程表单
  };
  processRoleSet: any = null; // 权限设置部分 组件绑定信息
  processFormSet: any = null; // 表单管理部分 组件绑定信息

  created() {
    this.currentData = this.$route.query.item ? JSON.parse(this.$route.query.item + "") : {};
    this.typeId = JSON.stringify(this.currentData) !== "{}" ? this.currentData.typeId : "";
  }
  // 下一步
  async handleNext() {
    if (this.state === 0) {
      this.state++;
      return true;
    }
    if (this.state === 1) {
      try {
        await Promise.all([
          this.saveProcessRole(api.processManage.saveProcessVisibleOrg, this.processRoleSet.orgValue, "可见组织"),
          this.saveProcessRole(api.processManage.saveProcessVisibleRole, this.processRoleSet.roleValue, "可见角色"),
          this.saveProcessRole(api.processManage.saveProcessVisibleUser, this.processRoleSet.userValue, "可见用户"),
          this.saveProcessType()
        ]);
        this.state++;
        return true;
      } catch (e) {
        return false;
      }
    }
    if (this.state === 2) {
      this.saveProcessForm().then(() => {
        this.state++;
        return;
      });
    }
    if (this.state === 3) {
      console.log(this.processFormSet);
    }
  }

  // 流程图设计完成
  flowChartSaved(data: any) {
    this.currentData = data;
    this.handleNext();
  }
  /**
   * 保存权限设置部分数据
   * @param url 对应保存地址
   * @param value 对应id数组
   * @param messageType 对应数据项名称
   */
  async saveProcessRole(url: string, value: any, messageType: string) {
    let {
      data: { data, status, message }
    } = await this.$http.post(url, {
      procdefKey: this.currentData.procdefKey,
      ids: value
    });
    if (status) {
      return true;
    } else {
      this.$message.warning(messageType + "保存失败，错误原因：" + message);
    }
  }
  // 保存请求 组织、角色、用户 列表数据
  saveProcessRoleList(data: any) {
    this.someRequestData.invisibleOrgList = data.orgList;
    this.someRequestData.invisibleRoleList = data.roleList;
    this.someRequestData.invisibleUserList = data.userList;
  }
  // 保存流程类型
  async saveProcessType() {
    let {
      data: { data, status, message }
    } = await this.$http.post(api.processManage.updateProcessType, {
      id: this.currentData.id,
      typeId: this.processRoleSet.type
    });
    if (status) {
      this.typeId = this.processRoleSet.type;
      return true;
    } else {
      this.$message.error("流程类型保存失败，错误原因：" + message);
    }
  }
  // 保存请求的 表单 列表数据
  saveProcessFormList(data: any) {
    this.someRequestData.processFormList = data;
  }
  // 判断是否使用表单模板
  async saveProcessForm() {
    return new Promise((resolve, reject) => {
      if (this.processFormSet.mode) {
        resolve(this.saveFormById());
      } else {
        (this.$refs.formDesign as any).save(async (formId: string) => {
          if (formId) {
            resolve(await this.saveFormById(formId));
          } else {
            reject();
          }
        });
      }
    });
  }
  // 保存表单流程对应关系
  async saveFormById(id?: string) {
    let {
      data: { data, status, message }
    } = await this.$http.post(api.processManage.saveProcessForm, {
      procdefKey: this.currentData.procdefKey,
      formId: id ? id : this.processFormSet.form.id
    });
    if (status) {
      return true;
    } else {
      this.$message.error(message);
      return false;
    }
  }

  // 发布流程
  async handleRelease() {
    let { status } = await (this.$refs.assignment as any).saveUserTaskDesignator();
    if (status) {
      this.$router.back();
    }
  }

  handlePrev() {
    this.state--;
  }
  handleCancel() {
    this.$confirm("流程未编辑完成，直接返回会导致流程无法使用，是否返回？", "提示", {
      distinguishCancelAndClose: true,
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$router.back();
    });
  }
}
